.Title {
    position: absolute;
    width: 100%;
    height: 100vh;
    background-image: url("../../assets/images/title-background.png");
    display: flex;
}

.Title h1 {
    position: absolute;
    font-size: 500%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    padding: 10px;
}